form {
  padding-top: 3em;
  padding-bottom: 3em;
  padding-left: 2em;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: wrap;
      flex-flow: wrap;
  -ms-flex-pack: center;
      justify-content: center;
}

/*form {
    display: flex;
    justify-content: center;

}*/
button {
    background-color: #4CAF50;
    color: white;
    padding: 14px 20px;
    margin: 8px 0;
    border: none;
    cursor: pointer;
    width: 17em;
    border: 5px solid white;
    border-radius: 25px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column;
        flex-flow: column;
    -ms-flex-align: center;
        align-items: center;
}

.add-form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 0.5 1;
      flex: 0.5 1;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  font-family: 'Raleway', sans-serif;
  background: url("../../assets/images/Banana Redux.png") center center no-repeat;
  background-size: cover;
}

.add-form h2 {
  /*flex: 0.4;*/
}

.add-form form {
  -webkit-animation: pulse 2s infinite;
          animation: pulse 2s infinite;
  background-color: hsla(0, 0%, 90%, 0.4);

  /*background: white;*/
  border-radius: 5px;
  border: 2px solid #555;
  color: black;
  padding: 1em 0 1em;
  width: 500px;
  margin-bottom: 1em;
  font-weight: 600;
  text-shadow: 1px 1px grey;
}

 form button {
  -moz-user-select: none;
  -ms-touch-action: manipulation;
  -ms-user-select: none;
  -webkit-appearance: button;
  -webkit-transition: all .3s cubic-bezier(.645,.045,.355,1);
  -webkit-user-select: none;
  background-color: #333;
  background-image: none;
  border: 1px solid #555;
  border-radius: 4px;
  color: #fff;
  cursor: pointer;
  /*display: inline-block;*/
  font-size: 14px;
  font-weight: 500;
  height: 28px;
  line-height: 1.15;
  margin-top: 20px;
  margin-bottom: 0;
  outline: 0;
  padding: 5px 30px 20px;
  position: relative;
  text-align: center;
  touch-action: manipulation;
  -webkit-transition: all .4s cubic-bezier(.645,.045,.355,1);
  -o-transition: all .4s cubic-bezier(.645,.045,.355,1);
  transition: all .4s cubic-bezier(.645,.045,.355,1);
  user-select: none;
  white-space: nowrap;
  font-family: 'Raleway', sans-serif;
  /*margin-left: 98px;*/
}

 button:hover {
  background-color: purple;
  border-color: purple;
}

@-webkit-keyframes pulse {
  0% {
    -webkit-box-shadow: 0 0 0 0 rgba(97,218,251, 0.4);
  }
  70% {
    -webkit-box-shadow: 0 0 0 90px rgba(97,218,251, 0);
  }
  100% {
    -webkit-box-shadow: 0 0 0 0 rgba(97,218,251, 0);
  }
}

@keyframes pulse {
  0% {
    -webkit-box-shadow: 0 0 0 0 rgba(97,218,251, 0.4);
            box-shadow: 0 0 0 0 rgba(97,218,251, 0.4);
  }
  70% {
    -webkit-box-shadow: 0 0 0 20px rgba(97,218,251, 0);
            box-shadow: 0 0 0 20px rgba(97,218,251, 0);
  }
  100% {
    -webkit-box-shadow: 0 0 0 0 rgba(97,218,251, 0);
            box-shadow: 0 0 0 0 rgba(97,218,251, 0);
  }
}


@media handheld, screen and (max-width: 750px) {

  .mood {
    background-color: hsla(0, 0%, 90%, 0.8);
    /*width: 100%;*/
    color: black;
    height: 70em;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    margin: 0em 8em 0em 8em;
    border-radius: 15px;
    font-size: 1.7em;
    letter-spacing: 1px;
    font-weight: bold;
  }

}

@media handheld, screen and (max-width: 440px) {

  .form {
    background-color: hsla(0, 0%, 90%, 0.8);
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
  }

  .mood {
    height: 108em;
  }

  .behaviors {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column;
        flex-flow: column;
    text-align: center;
  }


}



